@import './scss/variables';

.theme-light {
  --main-background: #ffffff;
  --header-footer-color: #000000;
  --header-footer-bg-color: #d9d9d9;
  --header-footer-bg-color2: #a6a6a6;
  --menubar-background: #878787;
  --menubar-color: #000000;
  --active-menu-color: #cccccc;
}

.theme-dark {
  --main-background: #f5f5f5;
  --header-footer-color: #ffffff;
  --header-footer-bg-color: #6f6f88;
  --header-footer-bg-color2: #3b3b48;
  --menubar-background: #525262;
  --menubar-color: #f5f5f5;
  --active-menu-color: #919191;
}

.theme-Turquoise {
  --main-background: #f5f5f5;
  --header-footer-color: #ffffff;
  --header-footer-bg-color: #6cc7cf;
  --header-footer-bg-color2: #03767e;
  --menubar-background: #2ab1be;
  --menubar-color: #f5f5f5;
  --active-menu-color: #ffffff;
}

.theme-green {
  --main-background: #f5f5f5;
  --header-footer-color: #ffffff;
  --header-footer-bg-color: #73dfae;
  --header-footer-bg-color2: #1e8455;
  --menubar-background: #32b67a;
  --menubar-color: #135436;
  --active-menu-color: #b3eed3;
}

.theme-blue {
  --main-background: #f5f5f5;
  --header-footer-color: #ffffff;
  --header-footer-bg-color: #83B3FF;
  --header-footer-bg-color2: #0047B6;
  --menubar-background: #007ad9;
  --menubar-color: #f5f5f5;
  --active-menu-color: #ffffff;
}

.theme-rose {
  --main-background: #f5f5f5;
  --header-footer-color: #ffffff;
  --header-footer-bg-color: #79425a;
  --header-footer-bg-color2: #79425a;
  --menubar-background: #935b74;
  --menubar-color: #f5f5f5;
  --active-menu-color: #ffffff;
}

.theme-teal {
  --main-background: #f5f5f5;
  --header-footer-color: #ffffff;
  --header-footer-bg-color: #427976;
  --header-footer-bg-color2: #427976;
  --menubar-background: #599592;
  --menubar-color: #f5f5f5;
  --active-menu-color: #003331;
}

html,
body {
  margin: 0;
}

.ng-main {
  background-color: var(--main-background);
}

.ng-header,
.ng-footer {
  height: 50px;
  background-image: -webkit-gradient(linear, left top, right top, from(var(--header-footer-bg-color)), to(var(--header-footer-bg-color2)));
  color: var(--header-footer-color);
}

.ng-header-right {
  height: 50px;
}

.ng-header a {
  color: var(--header-footer-color);
}

.ng-header a:hover {
  color: var(--header-footer-color);
}

.ng-header .pi {
  color: #000000;
}

.ng-footer div {
  text-align: center;
  padding: 10px;
}

.ng-content {
  padding: 10px;
  padding-left: 240px;
  margin-top: 50px;
}

.hidden-menu-content {
  padding-left: 10px;
}

.ng-content .ui-card {
  min-height: 100%;
  background: #f7f9fa;
}

.icon {
  line-height: 50px;
  font-size: 20px;
  cursor: pointer;
  padding: 10px;
}

.prime-button {
  width: 150px !important;
}

.overview-box-1 .ui-card-body {
  background-color: #f39611;
}

.overview-box-2 .ui-card-body {
  background-color: #326ddf;
}

.overview-box-3 .ui-card-body {
  background-color: #e02364;
}

.overview-box-4 .ui-card-body {
  background-color: #7dc931;
}

.color-red {
  color: red;
}

.header-breadcrumb {
  margin-bottom: 10px;
}

.login-panel,
.user-regisration {
  height: 100%;
  background-image: radial-gradient(var(--header-footer-bg-color) 40%, var(--header-footer-bg-color2));
}

.badge {
  display: inline-block;
  min-width: 10px;
  padding: 3px 7px;
  font-size: 12px;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  background-color: red;
  border-radius: 10px;
}

.login-logo {
  height: 100%;
  width: 100%;
}

.toggle-menu-button {
  text-align: center;
  height: 50px;
  width: 50px;
  background-color: var(--menubar-background);
}

.toggle-menu-button .icon {
  padding: 0;
  color: var(--menubar-color) !important;
}

.logo-large {
  display: block;
  height: 50px;
  width: 180px;
}

.logo-small {
  display: none;
}

.user-name {
  font-size: 15px;
  padding: 5px;
}

.notification-message {
  min-height: 25px;
  width: 200px;
  padding: 5px;
}

.hidden {
  display: none;
}

.np-loader {
  position: fixed;
  z-index: 1100;
  height: 2em;
  width: 2em;
  overflow: show;
  margin: auto;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.np-loader:before {
  content: "";
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}

.fa-fw {
  width: 50px;
}

.right {
  float: right;
}

.text-center {
  text-align: center;
}

.left {
  float: left;
}

.np-menubar {
  overflow: auto;
  padding: 0px !important;
  margin-top: 50px;
  width: 230px !important;
}

.np-sub-menu {
  padding: 0px;
  width: 100%;
  list-style: none;
  position: relative;
}

.np-menu {
  padding: 5px;
  margin: 0px;
}

.np-menu li a {
  padding: 7px;
  color: var(--menubar-color);
  display: block;
  text-decoration: none !important;
  position: relative;
}

.np-menu li a:hover,
.np-menu .active {
  color: var(--active-menu-color);
  cursor: pointer;
}

.np-menu .fa {
  width: 25px;
  text-align: center;
}

.np-menu li ul {
  display: block;
}

.np-menu li ul::before {
  content: "";
  display: block;
  position: absolute;
  z-index: 1;
  left: 20px;
  top: 0;
  bottom: 0;
  border-left: 1px solid $block-background-color;
}

a {
  cursor: pointer;
}

.extra-page-footer {
  position: relative;
  bottom: 0px;
  padding: 10px;
  height: 50px;
  text-align: center;
  width: 100%;
  color: white;
}

.ui-messages ul {
  display: inline-block !important;
}

.ui-fluid .ui-dropdown,
.ui-fluid p-dropdown {
  width: auto;
  min-width: 140px;
}

.ui-fluid .ui-inputtext {
  width: auto;
  min-width: 140px;
}

.ui-fluid .ui-button {
  width: auto;
}

.required:after {
  content: " *";
  color: red;
}

ol>li {
  padding: 1vh 1vw;
}

.font-green {
  color: #28a745;
}

.font-red {
  color: #dc3545;
}

.fong-blue {
  color: #007bff;
}

.p-field>label {
  display: block;
}

/* Medium devices (landscape tablets, 992px or less) */

@media only screen and (max-width: 992px) {
  .logo-large {
    display: none;
  }

  .logo-small {
    display: block;
    height: 50px;
    width: 50px;
  }

  .user-name {
    display: none;
  }

  .ng-content {
    padding-left: 10px;
  }

  .ng-header-left {
    width: 100px;
  }
}

.error-box {
  max-width: 900px;
  margin: 0 auto;
  height: 100%;
  overflow: auto;
  color: var(--header-footer-bg-color2);
}

.ui-toast-detail {
  white-space: pre-line;
}

.ui-toast-icon {
  font-size: 2rem;
  margin: 0 5px 0 0;
}

.p-toast-icon-close {
  width: 15px !important;
  height: 15px !important;
  position: absolute !important;
  top: 5px;
  right: 5px;
}

.p-toast {
  opacity: 0.99;
  z-index: 99;
}

.pInputNumber-width45 {
  width: 45px;
}
.pInputNumber-width75 {
  width: 75px;
}
